.atbd_content_active #directorist.atbd_wrapper {
  .select2-selection {
    display: block;
    width: 100%;
    padding: 0.375rem 0.75rem;
    font-size: 15px;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #dbdaf7;
    border-radius: 0.25rem;
    min-height: 44px;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }
  &.directory_search_area {
    .select2-selection {
      height: 49px;
    }
    .select2-container--default
      .select2-selection--single
      .select2-selection__rendered {
      line-height: 47px;
    }
  }
  .select2-container--default
    .select2-selection--single
    .select2-selection__rendered {
    line-height: 41px;
  }
  .select2-container--default
    .select2-selection--single
    .select2-selection__arrow {
    height: 35px;
    right: 10px;
    top: 0;
  }
  .select2-container--default
    .select2-selection--single
    .select2-selection__placeholder {
    color: $text-color;
  }
  .atbd_search_title_area {
    text-align: center;
    margin-bottom: 30px;
    h2.title {
      font-size: 28px;
    }
    p.sub_title {
      font-size: 18px;
      text-align: center;
    }
  }
  .single_search_field {
    .search_fields {
      line-height: 35px;
      height: 43px;
    }
    .select2-container--default
      .select2-selection--single
      .select2-selection__arrow {
      height: 48px;
    }
  }
  .atbd_submit_btn_wrapper {
    margin-top: 20px;
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    .btn_search,
    .more-filter {
      padding: 13px 20px;
      margin: 5px;
      span {
        margin-right: 7px;
      }
    }
    .more-filter {
      background: #fff;
      border: 1px solid #686e77;
      &:hover {
        color: inherit !important;
        background: #fff;
      }
    }
  }
  .atbd_seach_fields_wrapper {
    padding: 30px;
    border: 1px solid $border-color;
    .atbdp-search-form {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-left: -5px;
      margin-right: -5px;
      .single_search_field {
        flex: 1;
        padding-left: 5px !important;
        padding-right: 5px !important;
        .form-control {
          width: 100%;
        }
        .select2.select2-container {
          width: 100% !important;
        }
      }
    }
  }
  .directory_home_category_area {
    text-align: center;
    margin-top: 60px;
    > p {
      margin-bottom: 25px;
      font-size: 18px;
      text-align: center;
    }
    .categories {
      li {
        display: inline-block;
        border: 1px solid $border-color;
        text-align: center;
        border-radius: 3px;
        padding: 10px 20px;
        margin-bottom: 15px;
        margin-left: 7px;
        margin-right: 7px;
        a p {
          text-align: center;
        }
      }
    }
  }
}
